import axios from "utils/http";

//获取房间列表
export const room_get = (params) => {
  return axios.get(`/admin/room-list`, params);
};

//新增房间列表
export const room_add = (params) => {
  return axios.post(`/admin/room-list`, params);
};

//修改房间列表
export const room_update = (params) => {
  return axios.put(`/admin/room-list`, params);
};

//删除房间列表
export const room_del = (params) => {
  return axios.delete(`/admin/room-list`, params);
};
//返回房间名
export const room_get_by_name = (params) => {
  return axios.get(`/admin/room-list-by-name`, params);
};

