import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import {  personal_adjust_cd_config_update } from "../../../api/room";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, info } = props;
  const [form] = Form.useForm();
  //提交
  const submit = async () => {
    // console.log(form.getFieldsValue());
    try {
      form.validateFields().then(async () => {
        await personal_adjust_cd_config_update({
          ...form.getFieldsValue(),
        });
        form.resetFields();
        message.success("修改成功");
        onOk();
      });
    } catch (e) {
      console.error(e);
    }
  };
  const FormConfig = {
    labelCol: { span: 6 },
    wrapperCol: { span: 18 },
  };

  const on_cancel = () => {
    form.resetFields();
    onCancel();
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        ...info,
      });
    }
  }, [visible]);
  return (
    <Modal
      title="修改配置"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="类型"
          name="type"
          rules={[{ required: true, message: "请选择类型" }]}
        >
          <Select placeholder="请选择类型">
            <Option value={1}>1-上次触发并成功后的战斗次数</Option>
            <Option value={2}>2-上次触发并强制中断后的战斗次数</Option>
          </Select>
        </Form.Item>

        <Form.Item
          label="最大值"
          name="value_max"
          rules={[{ required: true, message: "请输入最大值" }]}
        >
          <InputNumber  placeholder="请输入最大值" />
        </Form.Item>

        <Form.Item
          label="最小值"
          name="value_min"
          rules={[{ required: true, message: "请输入最小值" }]}
        >
          <InputNumber  placeholder="请输入最小值" />
        </Form.Item>
        <Form.Item
          label="描述"
          name="desc"
          rules={[{ required: true, message: "请输入描述" }]}
        >
          <TextArea rows={4} placeholder="请输入描述" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
