import { Table, Button, Space } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import { personal_adjust_cd_config_get } from "../../../api/room";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      personal_adjust_cd_config_get_handle(newVal);
    },
  }));
  const {
    modifypassword_h,
    modifyaccountRole_h,
    block_h,
    pageSize = 10,
    total = 0,
    showTotal = true,
    update_h,
  } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });
  //类型转换
  const trans_type_text = (type) => {
    let text;
    switch (type) {
      case 1:
        text = "1-上次触发并成功后的战斗次数";
        break;
      case 2:
        text = "2-上次触发并强制中断后的战斗次数";
        break;
    }
    return text;
  };
  const columns = [
    {
      title: "类型",
      dataIndex: "type",
      key: "type",
      render: (_, record) => <span>{trans_type_text(record.type)}</span>,
    },
    { title: "最小值", dataIndex: "value_min" },
    { title: "最大值", dataIndex: "value_max" },
    {
      title: "描述",
      dataIndex: "desc",
    },
    {
      title: "操作",
      dataIndex: "action",
      render: (_, record) => (
        <Space>
          <a onClick={() => update_h(record)}>编辑</a>
        </Space>
      ),
    },
  ];
  //获取列表
  const personal_adjust_cd_config_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await personal_adjust_cd_config_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      // console.log(data)
      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      // console.log(pagination);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    // console.log(pagination)
    setPagination({ ...pagination });
  };
  useEffect(() => {
    personal_adjust_cd_config_get_handle();
  }, [pagination.current, pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
